import { Component, ViewChild, ViewChildren, QueryList } from '@angular/core';
import { IonicPage, NavController } from 'ionic-angular';
import { Api } from '../../providers/api/api';
import {Storage} from "@ionic/storage";
import 'rxjs/Rx';
import * as _ from 'lodash';

import {
	Direction,
	StackConfig,
	Stack,
	Card,
	ThrowEvent,
	DragEvent,
	SwingStackComponent,
	SwingCardComponent
} from 'angular2-swing';

@IonicPage()
@Component({
	selector: 'page-play',
	templateUrl: 'play.html',
})
export class PlayPage {
	@ViewChild('cardStack') swingStack: SwingStackComponent;
	@ViewChildren('card') swingCards: QueryList<SwingCardComponent>;

	cards: any[];
	stackConfig: StackConfig;
	popies: any[];
	isLoading: boolean = true;
	userId : number;

	constructor(public navCtrl: NavController, 
    public storage: Storage,
    public api: Api) {
		this.stackConfig = {
			// Default setting only allows UP, LEFT and RIGHT so you can override this as below
			allowedDirections: [
				Direction.LEFT,
				Direction.RIGHT
			],
			throwOutConfidence: (offsetX, offsetY, element) => {
				return Math.min(Math.abs(offsetX) / (element.offsetWidth / 2), 1);
			},
			transform: (element, x, y, r) => {
				this.onItemMove(element, x, y, r);
			},
			throwOutDistance: (d) => {
				return 800;
			}
		};

          this.storage.get('user').then((val) => {
            if (val) {
            	this.userId = val.id;
              	this.addPopies(2);
        }
    })  
	}

	ngAfterViewInit() {
		this.cards = [];
		this.popies = [
			/*{
				id: 1,
				usualName: 'Hieu Pham',
				age: 29,
				job_title: 'UX/UI lover',
				image: 'assets/img/hieu.png'
			},
			{
				id: 2,
				usualName: 'Adam Saddler',
				age: 39,
				job_title: 'Ionic Team is awesome',
				image: 'assets/img/adam.png'
			},
			{
				id: 3,
				usualName: 'Ben Affleck',
				age: 30,
				job_title: 'Another awesome Ionic guy',
				image: 'assets/img/ben.png'
			},
			{
				id: 4,
				usualName: 'Max Payne',
				age: 35,
				job_title: 'Game character assasin',
				image: 'assets/img/max.png'
			},
			{
				id: 5,
				usualName: 'Big Mike',
				age: 31,
				job_title: 'All guys are awesome',
				image: 'assets/img/mike.png'
			},
			{
				id: 6,
				usualName: 'Perry',
				age: 41,
				job_title: 'Ionic again',
				image: 'assets/img/perry.png'
			}*/	];

		  		this.addNewCards(this.popies);
		// setTimeout(() => {
		// 	this.isLoading = false;
		// }, 3000);
	}

	// Called whenever we drag an element
	onItemMove(element, x, y, r) {
		let nope = element.querySelector('.stamp-nope');
		let like = element.querySelector('.stamp-like');
		let caculatedValue = Math.min(100, Math.abs(x) - 20) / 100;// 0 - 1

		if (x < 0 && Math.abs(x) > 20) {
			nope.style.opacity = caculatedValue;
		} else {
			like.style.opacity = caculatedValue;
		}

		element.style['transform'] = `translate3d(0, 0, 0) translate(${x}px, ${y}px) rotate(${r}deg)`;

		// Zoom effect for the cards underneath
		if (this.swingCards.toArray()[1] != undefined){
			let cardBehind = this.swingCards.toArray()[1].getNativeElement();
			cardBehind.style['transform'] = `scale(${0.94 + 0.06 * caculatedValue}, ${0.94 + 0.06 * caculatedValue})`;
		}
	}

	addPopies(count){
		let popies: any;
        popies = {};

		this.api.post('getGamePopies/' + this.userId + '/' + count, popies)
          .subscribe(
              (popies) => {
                let body: any;
                body = JSON.parse(popies.text());
                for(let popy in body){
                	this.popies.push({
                		id: +popy,
                		image: body[popy]['image'],
                		usualName : body[popy]['user']['usualName']
                	});
                }

		  		this.addNewCards(this.popies);
              },
              (err) => {
                console.log(err);
                  alert("ko");
                  //this.navCtrl.setRoot(this.navCtrl.getActive().component);
              },
              () => {
                  //this.goToHome();
              }
          );
	}

	// Add new cards to our array
	addNewCards(popies) {
		console.log(this.cards.filter(item => popies.indexOf(item) >= 0));
		this.cards.push(...popies);

		console.info('CURRENT STACK:', this.cards.map(c => c.usualName));
	}

	disliked() {
		let removedCard = this.cards.shift();

		console.log('You disliked: ' + removedCard.usualName);


		this.getMoreCardsIfNecessary();
	}

	liked() {
		let removedCard = this.cards.shift();
		//this.checkMatching(removedCard);

		console.log('You liked: ' + removedCard.usualName);

		let pop: any;
        pop = {};

		this.api.post('pop/' + this.userId + '/' + removedCard.id, pop)
          .subscribe(
              (pop) => {
                let body: any;
                body = JSON.parse(pop.text());
                console.log(body);
              },
              (err) => {
                console.log(err);
                  alert("ko");
                  //this.navCtrl.setRoot(this.navCtrl.getActive().component);
              },
              () => {
                  //this.goToHome();
              }
          );
		this.getMoreCardsIfNecessary();
	}

	getMoreCardsIfNecessary() {
		if (this.cards.length < 2) {
			//console.log("ADD");
			this.addPopies(2);
		}
	}

	trackByFn(index, item) {
		return item.id;
	}

	goTo(page, direction) {
		this.navCtrl.push(page, {}, {
			direction: direction
		});
	}

}



