import { Component } from '@angular/core';
import { App, IonicPage, NavController, NavParams, ViewController, AlertController } from 'ionic-angular';
import { Api } from '../../providers/api/api';
import { ImagePicker } from '@ionic-native/image-picker';
import {Storage} from "@ionic/storage";
import {LoginPage} from "../login/login";

@IonicPage()
@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class ProfilePage {

  user: any;
  accountImage: string;
  usualName: string;
  description: string;
  profileImages: any[4];

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public imagePicker: ImagePicker,
    public app: App, 
    public api: Api,
    public storage: Storage,
    public alertCtrl: AlertController ) {
       this.storage.get('isConnected').then((val) => {
            if (!val) {
                this.navCtrl.setRoot(LoginPage);
            }
        });
       
        let data: any;
          data = {};

          this.storage.get('user').then((val) => {
            if (val) {
              this.api.post('getUser/'+val.id, data)
                  .subscribe(
                      (data) => {
                        let body: any;
                        body = JSON.parse(data.text());

                          this.accountImage =body['account_image'] 
                          this.usualName = body['usual_name'];
                          this.description = body['description'];
                          this.profileImages = body['popies'];
                      },
                      (err) => {
                        console.log(err);
                          alert("ko");
                          //this.navCtrl.setRoot(this.navCtrl.getActive().component);
                      },
                      () => {
                          //this.goToHome();
                      }
                  );
            }
        });   
  }

  ionViewDidLoad() {
  }

  close() {
    this.viewCtrl.dismiss();
  }

  openPhotoPicker(index) {
    console.log(this.imagePicker);
    this.imagePicker.getPictures({ maximumImagesCount: 1 })
      .then((results) => {
        this.profileImages[index].image = results[0];
      }, (err) => { });
  }

  removeImage(index) {
    let alert = this.alertCtrl.create({
      title: 'Supprimer cette photo',
      message: 'Tu veux vraiment supprimer cette photo ?',
      buttons: [
        {
          text: 'Annuler',
          role: 'cancel',
          handler: () => {
          }
        },
        {
          text: 'Supprimer',
          handler: () => {
            this.profileImages[index] = '';
          }
        }
      ]
    });
    alert.present();
  }

  goTo(page, direction) {
    this.navCtrl.push(page, {}, {
      direction: direction
    });
  }

}



